import { SearchList01Icon, Menu01Icon } from '@hugeicons/core-free-icons'
import { HugeiconsIcon } from '@hugeicons/react'
import clsx from 'clsx'
import { useAside } from '../aside'

const HamburgerBtnMenu = ({
  className,
  type,
}: {
  className?: string
  type: 'product-quick-view' | 'sidebar-navigation'
}) => {
  const { open: openAside } = useAside()

  return (
    <button
      type="button"
      onClick={() => {
        openAside(type)
      }}
      className={clsx(
        '-m-2.5 flex cursor-pointer items-center justify-center rounded-full p-2.5 hover:bg-neutral-200 focus-visible:outline-0 dark:hover:bg-neutral-700',
        className,
      )}
    >
      <span className="sr-only">Open main menu</span>
      <HugeiconsIcon
        icon={type === 'product-quick-view' ? Menu01Icon : SearchList01Icon}
        size={32}
        color="currentColor"
        strokeWidth={2}
      />
    </button>
  )
}

export default HamburgerBtnMenu
