const BottomNavbar = ({ isFr }: { isFr: boolean }) => {
  const { open: openAside } = useAside()

  const location = useLocation()
  const activeTab = location.pathname
  const navigate = useNavigate()

  const navItems = isFr ? navItemsFr : navItemsAr

  const onTabChange = (tab: (typeof navItems)[0]) => {
    if (tab.open) openAside('sidebar-navigation')
    else if (tab.external) window.location.href = tab.path
    else {
      if (tab.path === activeTab)
        window.scrollTo({ top: 0, behavior: 'smooth' })

      navigate({ to: tab.path })
    }
  }

  const scrollDirection = useScrollDirection()

  return (
    <div
      dir={isFr ? 'ltr' : 'rtl'}
      className={cn(
        'fixed bottom-0 left-0 right-0 z-50 sm:hidden transition-transform duration-300 ease-in-out ',
        scrollDirection === 'down' ? 'translate-y-full' : 'translate-y-0',
      )}
    >
      <div className="backdrop-blur-xl backdrop-saturate-[180%] bg-card/80 border-t border-border max-w-screen">
        <div className="grid grid-cols-5 items-center  max-w-md mx-auto">
          {navItems.map((item) => {
            const Icon = item.icon
            const isActive = activeTab === item.path

            return (
              <button
                key={item.path}
                onClick={() => onTabChange(item)}
                className={cn(
                  'flex flex-col items-center justify-center p-3 rounded-2xl transition-all duration-300 ease-outrelative cursor-pointer',
                  'hover:scale-110 active:scale-95',
                  isActive
                    ? 'text-lime-600  '
                    : 'text-muted-foreground hover:text-foreground',
                )}
              >
                <Icon
                  size={isActive ? 20 : 18}
                  className={cn(
                    ' transition-all duration-300 ease-out',
                    isActive && '',
                  )}
                />
                <span
                  className={cn(
                    'text-[10px] mt-1 font-medium  transition-all duration-300 ease-out whitespace-nowrap',
                    isActive ? 'opacity-100' : 'opacity-70',
                  )}
                >
                  {item.label}
                </span>
              </button>
            )
          })}
        </div>
      </div>
    </div>
  )
}

export default BottomNavbar

