import { cn } from '@/lib/utils'
import { navigationMenu, navigationMenuAr } from './right-sidebar-navigation'
import { BadgeButton } from '@/shared/Badge'
import { homeCategoriesAr, homeCategoriesFR } from '@/types'

interface HeaderProps {
  bottomBorder?: boolean
  className?: string
}

const Header: FC<HeaderProps> = ({ bottomBorder, className }) => {
  // const megamenu = await getNavMegaMenu()
  // const featuredPosts = (await getAllPosts()).slice(0, 2)
  const pathname = useLocation().pathname

  const scrollDirection = useScrollDirection()

  const isFR = useIsFr()

  function isHome() {
    if (
      pathname === '/news/fr' ||
      pathname === '/news/fr/' ||
      pathname === '/news' ||
      pathname === '/news/'
    )
      return true

    const param = pathname
      .replace('/news/fr/', '')
      .replace('/news/', '')
      .replace('/', '')

    // console.log('location', location)
    return (
      homeCategoriesFR.has(param) ||
      homeCategoriesAr.has(decodeURIComponent(param))
    )
  }

  const showNav = isHome()

  return (
    <div
      className={cn(
        isFR ? 'font-family-be-vietnam-pro' : 'font-family-Kufi',
        className,
      )}
      dir={isFR ? 'ltr' : 'rtl'}
    >
      <header
        className={clsx(
          'fixed top-0 left-0 right-0 z-45 transition-transform duration-300 ease-in-out bg-[#eef5fd] dark:bg-neutral-800 border-neutral-200 dark:border-neutral-700  ',
          bottomBorder && 'border-b',
          !bottomBorder && 'has-[.header-popover-full-panel]:border-b',
          scrollDirection === 'down' ? '-translate-y-full' : 'translate-y-0',
        )}
      >
        <div className="container flex h-20 justify-between gap-x-2.5">
          {/* Left section */}
          <div className="flex items-center ">
            <div className={cn('mx-2.5 ', isFR ? ' -ml-0.5' : ' -mr-0.5')}>
              <HamburgerBtnMenu type="product-quick-view" />
            </div>
            {pathname.startsWith('/news') && (
              <Logo label={isFR ? 'News' : 'الأخبار'} />
            )}
          </div>

          {/* Right section */}
          <div className="ms-auto flex items-center justify-end gap-x-2">
            {/* <MegaMenuPopover megamenu={megamenu} featuredPosts={featuredPosts} className="hidden lg:block" /> */}
            {/*<div className="ms-6 me-3 hidden h-10 border-l-2 lg:block" />*/}
            <div className="hidden sm:block">
              <Button className="h-10 px-3! cursor-pointer" plain>
                <PlusIcon className="size-5!" />
                <a
                  href={`https://ana.fibladi.com/${isFR ? 'fr' : 'ar'}/ads/create/`}
                  className="font-semibold text-base"
                >
                  {isFR ? 'Publiez votre annonce' : 'أنشر أعلانك'}
                </a>
              </Button>
            </div>

            {/*<div className="-ms-1.5">
            <SearchModal type="type1" />
          </div>*/}
            {/*<LangSwitch />*/}

            <Application isFr={isFR} scrollDirection={scrollDirection} />

            {/* <NotifyDropdown className="me-3" /> */}
            {/* <AvatarDropdown /> */}
            <div className="ms-2.5 hidden sm:flex ">
              <HamburgerBtnMenu type="sidebar-navigation" />
            </div>
          </div>
        </div>
      </header>
      {showNav && <Navigation2 isFR={isFR} />}
    </div>
  )
}

export default Header

