const Navigation2 = ({ isFR }: { isFR: boolean }) => {
  const location = useLocation()

  return (
    <nav className={'container w-full border-border hidden lg:block '}>
      <div className="    pt-20 font-medium ">
        <ul className="flex  items-center justify-between  py-4 ">
          {(isFR ? navigationMenu : navigationMenuAr).map((item) => {
            if (item.id == 0) {
              return null
            } else if (item.href.includes('http')) {
              return (
                <li
                  key={item.href}
                  className={cn(
                    'relative   flex flex-col text-center justify-center ',
                  )}
                >
                  <BadgeButton
                    color={
                      decodeURIComponent(location.pathname) === item.href
                        ? 'lime'
                        : 'none'
                    }
                  >
                    <a
                      href={item.href}
                      className="  text-xs xl:text-sm font-medium py-0.25"
                    >
                      {item.name}
                    </a>
                  </BadgeButton>
                </li>
              )
            } else {
              return (
                <li
                  key={item.href}
                  className={cn(
                    'relative  flex flex-col text-center justify-center ',
                    // decodeURIComponent(location.pathname) === item.href
                    //   ? 'dark:text-white'
                    //   : 'dark:text-slate-950',
                  )}
                >
                  <BadgeButton
                    className="whitespace-nowrap"
                    to={item.href}
                    color={
                      decodeURIComponent(location.pathname) === item.href ||
                      decodeURIComponent(location.pathname).slice(0, -1) ===
                        item.href
                        ? 'lime'
                        : 'none'
                    }
                  >
                    {item.id == 8 && (
                      <div
                        className="w-5 h-5 xl:w-6 xl:h-6  "
                        style={{ color: '#FF1744' }}
                        dangerouslySetInnerHTML={{
                          __html: trendimg,
                        }}
                      />
                    )}
                    {item.id == 9 && (
                      <div
                        className="w-4 h-4  xl:w-5 xl:h-5 "
                        style={{ color: '#F502E4' }}
                        dangerouslySetInnerHTML={{
                          __html: lastimg,
                        }}
                      />
                    )}
                    <span className="text-xs xl:text-sm font-medium py-0.25">
                      {item.name}
                    </span>
                  </BadgeButton>
                </li>
              )
            }
          })}
        </ul>
      </div>
    </nav>
  )
}
